body{
    margin: 0;
    height: 100vh;
    padding: 0;
    align-items: center;
    display: grid;
    justify-content: center;
}
.menu{
    background: url(./background.jpg);
    background-size: cover;
    border-radius: 40px;
    color: white;
    height: 500px;
    // display: grid;
    width: 500px;
    .menu-title{
        display: flex;
        justify-content: center;
        padding-top: 70px;
    }
    .content{
        display: grid;
        align-items: center;
        padding-top: 50px;
        justify-content: center;
        > .menu-input{
            margin-bottom: 50px;
            input {
                border-radius: 5px !important;
            }
        }
        input {
            background: white;
        }
    }
}